<template>
  <List border>
    <ListItem v-for="(mp3, index) in this.mp3List" @click="play(index)">
      {{index+1}}、{{truncatedText(mp3.title)}}
      <br>
      <span style="color: cadetblue;">{{mp3.duration}}</span>
    </ListItem>
  </List>
</template>

<script>
import axios from "axios";
import {getAuthorList} from "@/api/api";

export default {

  computed: {
    doubleCount () {
      return this.$store.getters.doubleCount
    }
  },


  data () {
    return {
      dataUrl : '',
      mp3List:[]
    }
  },


  async mounted() {
    const url =this.$route.query.url
    this.dataUrl = url
    const response = await axios.get(url);
    this.mp3List = response.data
  },

  methods: {
    truncatedText(text) {
      if (text && text.length > 10) {
        return text.substring(0, 15) + '...';
      }
      return text || '';
    } ,


    async play(index) {
      //alert(mp3Url)
      this.$eventBus.emit('some-event', { index: index, mp3List: this.mp3List });
    },

    increment () {
      this.$store.commit('increment')
    },
    incrementAsync () {
      this.$store.dispatch('incrementAsync')
    }
  }
}
</script>
