<template>
  <div class="music-player">
    <!-- 音频播放器 -->
    <audio ref="audioPlayer" :src="currentSong.url" @timeupdate="updateProgress" @loadedmetadata="setDuration"></audio>

    <!-- 歌曲信息 -->
    <p>{{ truncatedText() }}</p>

    <!-- 播放控制按钮 -->
    <div class="controls">
      <span @click="prevSong" class="control-btn">&#9664;</span>
      <span @click="playPause" class="control-btn">{{ isPlaying ? '暂停' : '播放' }}</span>
      <span @click="nextSong" class="control-btn">&#9654;</span>
      <p class="time" v-if="duration > 0">{{ formatTime(currentTime) }} / {{ formatTime(duration) }}</p>
    </div>

    <!-- 播放进度条 -->
    <div class="progress-bar" @click="seek">
      <div class="progress" :style="{ width: progressWidth }"></div>
      <div class="progress-indicator" :style="{ left: progressWidth }"></div>

    </div>
  </div>
</template>
<script>
export default {
  name: 'MusicPlayer',
  data() {
    return {
      audioPlayer: null,
      currentSongIndex: 0,
      currentSong: {},
      progressWidth: '0%',
      isPlaying: false,
      currentTime: 0,
      duration: 0,
      audioList: [],
    };
  },

  mounted() {
    this.$eventBus.on('some-event', (data) => {
      this.audioPlayer.pause();
      this.isPlaying = false;
      this.currentSongIndex = data.index;
      this.audioList = data.mp3List;
      this.currentTime = 0;
      this.currentSong = this.audioList[this.currentSongIndex];
      this.audioPlayer.src = this.currentSong.url;
      //this.playPause()
      this.audioPlayer.addEventListener('canplay', () => {
        this.isPlaying = true;
        this.audioPlayer.play();
      }, {once: true});
      // if (!this.isPlaying){
      //   this.isPlaying = true;
      //   this.audioPlayer.play();
      // }
    });

    this.audioPlayer = this.$refs.audioPlayer;
    // this.currentSong = this.audioList[this.currentSongIndex];
    // this.audioPlayer.addEventListener('timeupdate', this.updateProgress);
    //
    // // 添加touchstart事件监听器
    // document.addEventListener('touchstart', this.initPlayAudio, {once: true});
  },

  beforeUnmount() {
    this.$eventBus.off('some-event');
  },

  beforeDestroy() {
    this.audioPlayer.removeEventListener('timeupdate', this.updateProgress);
  },

  methods: {
    truncatedText() {
      if (this.currentSong.title && this.currentSong.title.length > 10) {
        return this.currentSong.title.substring(0, 15) + '...';
      }
      return this.currentSong.title || '';
    },

    playPause() {
      if (this.isPlaying) {
        this.audioPlayer.pause();
      } else {
        this.audioPlayer.play();
      }
      this.isPlaying = !this.isPlaying;
    },

    updateProgress() {
      this.currentTime = this.audioPlayer.currentTime;
      this.progressWidth = `${(this.currentTime / this.duration) * 100}%`;
    },

    formatTime(time) {
      const minutes = Math.floor(time / 60);
      const seconds = Math.floor(time % 60);
      return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
    },

    setDuration() {
      this.duration = this.audioPlayer.duration;
    },

    nextSong() {
      this.audioPlayer.pause();
      this.currentSongIndex = (this.currentSongIndex + 1) % this.audioList.length;
      this.currentSong = this.audioList[this.currentSongIndex];
      this.audioPlayer.src = this.currentSong.url;
      this.audioPlayer.addEventListener('canplay', () => {
        this.audioPlayer.play();
        this.isPlaying = true;
      }, {once: true});
    },

    prevSong() {
      this.audioPlayer.pause();
      this.currentSongIndex = (this.currentSongIndex - 1 + this.audioList.length) % this.audioList.length;
      this.currentSong = this.audioList[this.currentSongIndex];
      this.audioPlayer.src = this.currentSong.url;
      this.audioPlayer.addEventListener('canplay', () => {
        this.audioPlayer.play();
        this.isPlaying = true;
      }, {once: true});
    },

    initPlayAudio() {
      this.audioPlayer.play().catch(error => {
        console.error('Failed to play audio:', error);
      });
    },

    seek(event) {
      const progressBar = event.currentTarget;
      const clickPosition = event.offsetX / progressBar.offsetWidth;
      const newTime = clickPosition * this.duration;
      this.audioPlayer.currentTime = newTime;
      this.updateProgress();
    }
  },
};
</script>
<style scoped>
.music-player {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.controls {
  display: flex;
  justify-content: center;
}

.control-btn {
  font-size: 20px;
  cursor: pointer;
}

.progress-bar {
  width: 90%;
  height: 5px; /* Increase height for better visibility */
  background-color: #e0e0e0; /* Background color for better contrast */
  border-radius: 5px;
  position: relative; /* Required for absolute positioning of the progress indicator */
  cursor: pointer;
}

.progress {
  height: 100%;
  background-color: #17f5e2;
  border-radius: 5px;
  transition: width 0.2s ease;
}

.time {
  font-size: 14px;
  margin-top: 5px;
}

/* Add a small circle indicator for current progress */
.progress-indicator {
  width: 15px;
  height: 15px;
  background-color: #17f5e2;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: left 0.2s ease;
}
</style>
