<template>
  <Collapse v-model="value" simple accordion>
    <Panel v-for="(item, index) in this.AuthorList">
      {{item.channel}}
      <template #content>
        <List border>
          <ListItem v-for="(unit, unitIndex) in item.unit" @click="goTuList(unit.url)"
          >{{unit.published}}</ListItem>
        </List>

      </template>
    </Panel>
  </Collapse>

</template>

<script>
import { getAuthorList } from '@/api/api';
export default {
  data () {
    return {
      value: '脑洞乌托邦',
      AuthorList:[]
    }
  },
  async created() {
    try {
      const response = await getAuthorList();
      this.AuthorList = response.data
      //console.log(response.data)
    } catch (error) {
      console.error(error);
      this.message = 'An error occurred';
    }
  },
  methods: {
    goTuList(url) {
      this.$router.push('/mp3List?url='+url);
    }
  }
}
</script>
